import './App.less'

const One = () => (
    <div>
        <h2></h2>
    </div>
)

const Two = () => (
    <div>
        <h2></h2>
    </div>
)

const Lists = ({ match }) => (
    <div>
        <h3>{match.params.ListsId}</h3>
    </div>
)

const List = ({ match }) => (
    <div>
        <h2></h2>
        <ul>
            <li>
                <Link to={`${match.url}/`}>
                    
                </Link>
            </li>
            <li>
                <Link to={`${match.url}/`}>
                    
                </Link>
            </li>
            <li>
                <Link to={`${match.url}/`}>
                    
                </Link>
            </li>
        </ul>
        <Route path={`${match.url}/:ListsId`} component={Lists}/>
        <Route exact path={match.url} render={() => (
            <h3>url...</h3>
        )}/>
    </div>
)

function Loading(props) {
  if (props.error) {
    return <div>Error!</div>;
  } else if (props.pastDelay) {
    return <div>Loading...</div>;
  } else {
    return null;
  }
}

const LoadableTabs= Loadable({
  loader: () => import('./functions/Index/Tabs'),
	loading: Loading,
	delay: 300
});

const LoadableArticleDetail= Loadable({
  loader: () => import('./functions/ArticleDetail'),
	loading: Loading,
	delay: 300
});

const LoadableBookDetail= Loadable({
  loader: () => import('./functions/BookDetail'),
	loading: Loading,
	delay: 300
});

